html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  background: #ffffff;
  color: #1c1c21;
  font-family: 'Inter', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

button {
  display: block;
  border: none;
  background: none;
  outline: none;
}

h1,
ul,
p {
  margin: 0;
}

ul,
button {
  list-style: none;
  padding: 0;
}

.container {
  width: 100vw;
  min-height: 100vh;
  padding: 1.875rem 0;
}

.contentOverlay {
  padding-top: 20%;
}

@media (min-width: 48rem) {
  .container {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .contentOverlay {
    padding-top: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .contentBlock {
    width: 45%;
  }
}

@media (min-width: 75rem) {
  .contentOverlay {
    justify-content: space-evenly;
  }
  .contentBlock {
    width: 40%;
  }
}

.blankAnswerOverlay {
  width: 100%;
  position: relative;
  margin-bottom: 0.5rem;
}

.blankAnswerOverlay::before {
  position: absolute;
  top: 50%;
  display: block;
  content: '';
  width: 100%;
  height: 0.0625rem;
}

.blankAnswerOverlay_inactive::before {
  background: #d0d0d8;
}

.blankAnswer {
  margin: 0 auto;
  position: relative;
  width: 18rem;
  height: 3.5rem;
}

.blankAnswer:disabled {
  color: #1c1c21;
}

.blankAnswerSvg {
  width: 18rem;
  height: 3.5rem;
}

.blankAnswerOverlay_inactive::before :hover {
  background: #ff8b37;
}

.blankAnswerSvg_selected path {
  fill: #fff3eb;
  stroke: #ff8b37;
}

.blankAnswerOverlay_selected::before {
  background: #ff8b37;
}

.blankAnswerSvg_correct path {
  fill: #e6faea;
  stroke: #47d867;
}

.blankAnswerOverlay_correct::before {
  background: #47d867;
}

.blankAnswerSvg_wrong path {
  fill: #fdeeed;
  stroke: #ec6259;
}

.blankAnswerOverlay_wrong::before {
  background: #ec6259;
}

.blankAnswerText {
  max-width: 18rem;
  max-height: 3.5rem;
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  cursor: pointer;
  padding: 0.25rem 1.5rem;
  display: flex;
  align-items: center;
  font-size: 0.875rem;
  overflow: hidden;
}

.blankAnswerText span {
  text-transform: uppercase;
  font-weight: 600;
  color: #ff8b37;
  margin-right: 0.5rem;
}

@media (min-width: 62rem) {
  .blankAnswerSvg_inactive path:hover {
    stroke: #ff8b37;
  }

  .blankAnswerOverlay {
    width: 50%;
  }

  .blankAnswerOverlay:nth-of-type(3) {
    margin-bottom: 0;
  }

  .blankAnswerOverlay:nth-of-type(4) {
    margin-bottom: 0;
  }
}

@media (min-width: 75rem) {
  .blankAnswer {
    width: 23.3125rem;
    height: 4.5rem;
  }

  .blankAnswerSvg {
    width: 23.3125rem;
    height: 4.5rem;
  }

  .blankAnswerText {
    max-width: 23.3125rem;
    max-height: 4.5rem;
    padding: 0.5rem 32px;
    font-size: 20px;
  }
}

@media (min-width: 120rem) {
  .blankAnswerOverlay {
    width: 40%;
  }
}

.blankGradeOverlay {
  width: 100%;
  position: relative;
  margin-bottom: 0.5rem;
}

.blankGradeOverlay:first-of-type {
  margin-bottom: 0;
}

.blankGradeOverlay::before {
  position: absolute;
  top: 50%;
  display: block;
  content: '';
  width: 100%;
  height: 0.0625rem;
}

.blankGradeOverlay_disactive::before,
.blankGradeOverlay_normal::before {
  background: #d0d0d8;
}

.blankGradeOverlay_active::before {
  background: #ff8b37;
}

.blankGrade {
  margin: 0 auto;
  position: relative;
  width: 15rem;
  height: 2rem;
}

.blankGradeSvg {
  width: 15rem;
  height: 2rem;
}

.blankGradeSvg_active path {
  stroke: #ff8b37;
}

.blankGradeText {
  max-width: 15rem;
  max-height: 2rem;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  cursor: pointer;
  display: flex;
  align-items: center;
  font-size: 0.875rem;
  overflow: hidden;
}

.blankGradeText_active {
  color: #ff8b37;
}

.blankGradeText_disactive {
  color: #d0d0d8;
}

@media (min-width: 75rem) {
  .blankGrade {
    height: 2.5rem;
  }

  .blankGradeSvg {
    height: 2.5rem;
  }
}

.boardGrade {
  width: 100%;
  display: flex;
  flex-direction: column-reverse;
}

.gameContainer {
  width: 100vw;
  min-height: 100vh;
}

.gameContentBlock {
  width: 100vw;
  min-height: 100vh;
  padding: 18% 0 10%;
  position: relative;
  background-color: #f5f5f7;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.gameQuestion {
  margin: 0;
  padding: 0 1rem;
  text-align: center;
  font-weight: 600;
  font-size: 1.125rem;
  margin-bottom: 10%;
}

.boardBlock {
  width: 100vw;
  min-height: 100vh;
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #f5f5f7;
}

.boardBlock_active {
  display: flex;
}

.boardBlock_hidden {
  display: none;
}

.gameButton {
  position: absolute;
  top: 1.3125rem;
  right: 1.3125rem;
  background-repeat: no-repeat;
  background-size: contain;
  cursor: pointer;
}

.openBoardButton {
  width: 1rem;
  height: 0.875rem;
  background-image: url(/millionaire/static/media/burger.ef43c181.svg);
}

.closeBoardButton {
  width: 0.8125rem;
  height: 0.8125rem;
  background-image: url(/millionaire/static/media/close.51226034.svg);
}

@media (min-width: 48rem) {
  .gameContainer {
    display: flex;
    flex-wrap: nowrap;
  }

  .gameContentBlock {
    width: 65%;
    padding: 10% 0 10%;
  }

  .boardBlock {
    position: relative;
    width: 35%;
    background-color: #fff;
  }

  .boardBlock_hidden {
    display: flex;
  }

  .gameButton {
    display: none;
  }
}

@media (min-width: 62rem) {
  .gameContentBlock {
    width: 74%;
    padding: 10% 5%;
  }

  .gameQuestion {
    text-align: left;
    padding: 0;
  }

  .answersBlock {
    display: flex;
    flex-wrap: wrap;
  }

  .boardBlock {
    width: 26%;
  }
}

@media (min-width: 75rem) {
  .gameContentBlock {
    padding: 5% 5%;
  }

  .gameQuestion {
    font-size: 2rem;
  }
}

.logo {
  margin: 0 auto;
  width: 12.25rem;
  height: 9.6875rem;
  margin-bottom: 10%;
}

@media (min-width: 48rem) {
  .logo {
    margin: 0;
    margin-bottom: 0;
  }
}

@media (min-width: 75rem) {
  .logo {
    width: 28.25rem;
    height: 22.25rem;
  }
}

.mainButton {
  margin: 0 auto;
  width: 18rem;
  height: 3rem;
  border-radius: 0.5rem;
  background-color: #ff8b37;
  color: #fff;
  font-weight: 600;
  font-size: 0.875rem;
}

.mainButton:hover {
  background-color: #ffac70;
}

.mainButton:active {
  background-color: #e87928;
}

@media (min-width: 48rem) {
  .mainButton {
    position: relative;
    bottom: 0;
    left: 0;
    transform: translateX(0);
  }
}

@media (min-width: 62rem) {
  .mainButton {
    width: 18.5rem;
    height: 4rem;
    border-radius: 0.75rem;
    font-size: 1.25rem;
  }
}

.startContainer {
  background: linear-gradient(
    to bottom right,
    #fff 0%,
    #fff 50%,
    #fff3eb 50%,
    #fff3eb 100%
  );
}

.startTitle {
  margin: 0 auto;
  width: 90%;
  text-align: center;
  font-weight: 600;
  font-size: 2rem;
  margin-bottom: 20%;
}

@media (min-width: 48rem) {
  .startTitle {
    margin: 0;
    width: auto;
    text-align: left;
    margin-bottom: 5%;
  }
}

@media (min-width: 75rem) {
  .startTitle {
    font-size: 3.5rem;
    margin-bottom: 15%;
  }
}

.winContainer {
  background: #f5f5f7;
}
.winTitle {
  font-weight: 600;
  font-size: 1.125rem;
  color: #1c1c21;
  opacity: 0.5;
  text-align: center;
  margin-bottom: 0.5rem;
}
.winEarned {
  margin: 0 auto;
  width: 90%;
  text-align: center;
  font-weight: 600;
  font-size: 2rem;
  margin-bottom: 20%;
}

@media (min-width: 48rem) {
  .winTitle {
    text-align: left;
  }

  .winEarned {
    margin: 0;
    width: auto;
    text-align: left;
    margin-bottom: 5%;
  }
}

@media (min-width: 75rem) {
  .winLogoOverlay {
    margin-right: 15%;
  }

  .winTitle {
    font-size: 2rem;
  }

  .winEarned {
    width: 37.5rem;
    font-size: 3.5rem;
    margin-bottom: 15%;
  }
}

